/* visibility */
/*------------------------------------------------------------------------------------------------*/

[class-box~="hidden_"] > *{
  visibility: hidden;
}
[class-box~="hidden"] {
  visibility: hidden;
}

[class-box~="visible_"] > *{
  visibility: visible;
}
[class-box~="visible"] {
