/*------------------------------------------------------------------------------------------------*/
/* separator */
/*------------------------------------------------------------------------------------------------*/

$i: 1;
@while($i <= 64) {
  $value: $i*(1.0*$rempx);

  [class-box~="separator\(#{$i}\)-x"] {
    display: inline-block;
    width: $value;
    height: 100%;
  }
  [class-box~="separator\(#{$i}\)-y"] {
    display: block;
    width: 100%;
    height: $value;
  }

  $i: $i + 1;
}


/*------------------------------------------------------------------------------------------------*/
/* wrapping */
/*------------------------------------------------------------------------------------------------*/

[class-box~="wrap\(no\)_"] > * {
  white-space: nowrap;
}

[class-box~="wrap\(no\)"] {
  white-space: nowrap;
}

/*----------------------------------------------------------------*/

[class-box~="wrap\(yes\)_"] > * {
  white-space: normal;
}

[class-box~="wrap\(yes\)"] {
  white-space: normal;
}


